.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* css */

#root {
  background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
}

#bio,
#projects,
#skills {
  margin: 10% 10% 5% 10%;
  min-height: 43vh;
}

#contact {
  margin: 5% 10% 1% 10%;
}


.card {
  background-color: #fff;
  background-image: none;
  overflow: hidden;
  margin: 2%;
  width: 40%;
}

.skill-card {
  width: 25%;
  padding-bottom: 20%;
}

.skill-logo-title {
  padding-bottom: 20%;
}

.card:hover {
  transition: all 0.2s ease-in-out;
  transform: scale(1.03);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-grid {
  margin: auto;
  justify-content: center;
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.logo {
  margin-left: 2%;
}

.top-nav {
  background-image: none;
  opacity: 2;
  margin-bottom: 2%;
}

/* social icons */

.social-icon {
  margin: 2.5%;
}

.social-icon:hover .headshot:hover {
  transition: all 0.2s ease-in-out;
  transform: scale(1.03);
}

.topnav-link, .logo {
  border-bottom: 4px solid transparent;
  color: #000;
}

.topnav-link:hover, .logo:hover {
  border-bottom: 4px solid black;
}

#education {
  margin: 7.5% 10% 5% 10%;
  min-height: 25vh;
}

.edu-text {
  margin: 1.5% 20%;
  text-align: left;
}

.card-img {
  height: 80%;
  width: auto;
}


/* small screen projects grid  */

@media screen and (max-width: 800px) {
  .card-grid {
    display: block;
  }
  .card {
    width: 100%;
  }
}
